'use client';

import React, { useState, useEffect } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useTournaments } from '@/context/TournamentContext';
import { useApp } from '@/context/AppContext';
import Link from 'next/link';

export default function TournamentView() {
  const { tournaments, loading, error, deleteTournament, createTournament } = useTournaments();
  const { role, isAuthenticated } = useAuth();
  const { state } = useApp();
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [selectedPlayers, setSelectedPlayers] = useState<string[]>([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [suggestedBracketSize, setSuggestedBracketSize] = useState(32);
  const [stageRules, setStageRules] = useState({
    upper: 5,
    lower: 5,
    quarter: 6,
    semi: 7,
    final: 9
  });

  const players = state.players || [];
  
  // Debug: –ø–æ–∫–∞–∑–∞—Ç–∏ —Å–∫—ñ–ª—å–∫–∏ –≥—Ä–∞–≤—Ü—ñ–≤ —î
  console.log('TournamentView players count:', players.length, 'loading:', state.loading);

  // –ê–≤—Ç–æ–º–∞—Ç–∏—á–Ω–∏–π —Ä–æ–∑—Ä–∞—Ö—É–Ω–æ–∫ —Ä–æ–∑–º—ñ—Ä—É —Å—ñ—Ç–∫–∏ –ø—Ä–∏ –∑–º—ñ–Ω—ñ –∫—ñ–ª—å–∫–æ—Å—Ç—ñ –≥—Ä–∞–≤—Ü—ñ–≤
  useEffect(() => {
    if (selectedPlayers.length > 0) {
      const nextPowerOf2 = Math.pow(2, Math.ceil(Math.log2(selectedPlayers.length)));
      setSuggestedBracketSize(nextPowerOf2);
    }
  }, [selectedPlayers.length]);

  // Handle delete tournament
  const handleDeleteTournament = async (id: number) => {
    if (!confirm('–í–∏ –≤–ø–µ–≤–Ω–µ–Ω—ñ, —â–æ —Ö–æ—á–µ—Ç–µ –≤–∏–¥–∞–ª–∏—Ç–∏ —Ü–µ–π —Ç—É—Ä–Ω—ñ—Ä?')) {
      return;
    }

    try {
      await deleteTournament(id);
    } catch (err) {
      alert(err instanceof Error ? err.message : '–ù–µ –≤–¥–∞–ª–æ—Å—è –≤–∏–¥–∞–ª–∏—Ç–∏ —Ç—É—Ä–Ω—ñ—Ä');
    }
  };

  // Handle create tournament
  const handleCreateTournament = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const formData = new FormData(e.currentTarget);
    
    if (selectedPlayers.length < 2) {
      alert('–ü–æ—Ç—Ä—ñ–±–Ω–æ –º—ñ–Ω—ñ–º—É–º 2 –≥—Ä–∞–≤—Ü—ñ –¥–ª—è —Ç—É—Ä–Ω—ñ—Ä—É');
      return;
    }
    
    const upperBracketSize = parseInt(formData.get('upper_bracket_size') as string);
    const olympicSize = parseInt(formData.get('olympic_size') as string);
    
    try {
      await createTournament({
        name: formData.get('name') as string,
        description: formData.get('description') as string || '',
        player_ids: selectedPlayers,
        upper_bracket_size: upperBracketSize,
        olympic_size: olympicSize,
        stage_rules: stageRules,
      });
      setShowCreateModal(false);
      setSelectedPlayers([]);
    } catch (err) {
      alert(err instanceof Error ? err.message : '–ù–µ –≤–¥–∞–ª–æ—Å—è —Å—Ç–≤–æ—Ä–∏—Ç–∏ —Ç—É—Ä–Ω—ñ—Ä');
    }
  };

  // Toggle player selection
  const togglePlayer = (playerId: string) => {
    setSelectedPlayers(prev => 
      prev.includes(playerId) 
        ? prev.filter(id => id !== playerId)
        : [...prev, playerId]
    );
  };

  if (loading) {
    return <div className="text-center py-8">–ó–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è...</div>;
  }

  if (error) {
    return (
      <div className="text-center py-8">
        <p className="text-red-600">–ü–æ–º–∏–ª–∫–∞: {error}</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold text-gray-900">üèÜ –¢—É—Ä–Ω—ñ—Ä–∏</h1>
        {isAuthenticated && role === 'admin' && (
          <button
            onClick={() => setShowCreateModal(true)}
            className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 font-semibold"
          >
            ‚ûï –°—Ç–≤–æ—Ä–∏—Ç–∏ —Ç—É—Ä–Ω—ñ—Ä
          </button>
        )}
      </div>

      {/* Tournament List */}
      {tournaments.length === 0 ? (
        <div className="bg-white rounded-lg shadow-md p-8 text-center">
          <p className="text-gray-600 mb-4">–ü–æ–∫–∏ —â–æ –Ω–µ–º–∞—î —Ç—É—Ä–Ω—ñ—Ä—ñ–≤</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {tournaments.map(tournament => (
            <div
              key={tournament.id}
              className="bg-white rounded-lg shadow-md p-6 hover:shadow-lg transition-shadow"
            >
              <div className="flex justify-between items-start mb-3">
                <h3 className="text-xl font-bold text-gray-900">{tournament.name}</h3>
                <span className={`px-2 py-1 text-xs rounded-full ${
                  tournament.status === 'completed' ? 'bg-green-100 text-green-800' :
                  tournament.status === 'active' ? 'bg-blue-100 text-blue-800' :
                  'bg-gray-100 text-gray-800'
                }`}>
                  {tournament.status === 'completed' ? '–ó–∞–≤–µ—Ä—à–µ–Ω–æ' :
                   tournament.status === 'active' ? '–ê–∫—Ç–∏–≤–Ω–∏–π' : '–ß–µ—Ä–Ω–µ—Ç–∫–∞'}
                </span>
              </div>

              {tournament.description && (
                <p className="text-sm text-gray-600 mb-3">{tournament.description}</p>
              )}

              <div className="space-y-2 text-sm text-gray-700">
                <div className="flex justify-between">
                  <span>–§–æ—Ä–º–∞—Ç:</span>
                  <span className="font-semibold">{tournament.upper_bracket_size} ‚Üí {tournament.olympic_size}</span>
                </div>
                <div className="flex justify-between">
                  <span>–î–æ:</span>
                  <span className="font-semibold">{tournament.max_score} –æ—á–æ–∫</span>
                </div>
                <div className="flex justify-between">
                  <span>–°—Ç–≤–æ—Ä–µ–Ω–æ:</span>
                  <span className="font-semibold">{new Date(tournament.created_at).toLocaleDateString('uk-UA')}</span>
                </div>
              </div>

              <div className="mt-4 pt-4 border-t border-gray-200 flex gap-2">
                <Link
                  href={`/tournaments/${tournament.id}`}
                  className="flex-1 px-3 py-1 text-sm bg-blue-600 text-white rounded hover:bg-blue-700 text-center"
                >
                  –í—ñ–¥–∫—Ä–∏—Ç–∏
                </Link>
                {/* –ö–Ω–æ–ø–∫–∞ –≤–∏–¥–∞–ª–µ–Ω–Ω—è –¢–Ü–õ–¨–ö–ò –¥–ª—è admin (—Ç—Ä–µ–±–∞ –±—É—Ç–∏ –∑–∞–ª–æ–≥—ñ–Ω–µ–Ω–∏–º) */}
                {isAuthenticated && role === 'admin' && (
                  <button
                    onClick={() => handleDeleteTournament(tournament.id)}
                    className="px-3 py-1 text-sm bg-red-600 text-white rounded hover:bg-red-700"
                  >
                    –í–∏–¥–∞–ª–∏—Ç–∏
                  </button>
                )}
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Create Tournament Modal */}
      {showCreateModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-lg shadow-xl max-w-4xl w-full max-h-[90vh] overflow-y-auto p-6">
            <h2 className="text-2xl font-bold text-gray-900 mb-4">–°—Ç–≤–æ—Ä–∏—Ç–∏ –Ω–æ–≤–∏–π —Ç—É—Ä–Ω—ñ—Ä</h2>
            
            <form onSubmit={handleCreateTournament} className="space-y-6">
              {/* Tournament Info */}
              <div className="bg-gray-50 rounded-lg p-4 space-y-4">
                <h3 className="font-semibold text-gray-900">–Ü–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—è –ø—Ä–æ —Ç—É—Ä–Ω—ñ—Ä</h3>
                
                <div>
                  <label className="block text-sm font-medium text-gray-900 mb-1">
                    –ù–∞–∑–≤–∞ —Ç—É—Ä–Ω—ñ—Ä—É *
                  </label>
                  <input
                    type="text"
                    name="name"
                    required
                    placeholder="–ù–∞–ø—Ä–∏–∫–ª–∞–¥: –ó–∏–º–æ–≤–∏–π —Ç—É—Ä–Ω—ñ—Ä 2025"
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 text-gray-900 placeholder-gray-400"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-900 mb-1">
                    –û–ø–∏—Å (–Ω–µ–æ–±–æ–≤'—è–∑–∫–æ–≤–æ)
                  </label>
                  <textarea
                    name="description"
                    rows={2}
                    placeholder="–ö–æ—Ä–æ—Ç–∫–∏–π –æ–ø–∏—Å —Ç—É—Ä–Ω—ñ—Ä—É..."
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 text-gray-900 placeholder-gray-400"
                  />
                </div>
              </div>

              {/* Tournament Format */}
              <div className="bg-gray-50 rounded-lg p-4 space-y-4">
                <h3 className="font-semibold text-gray-900">–§–æ—Ä–º–∞—Ç —Ç—É—Ä–Ω—ñ—Ä—É</h3>
                
                {selectedPlayers.length > 0 && (
                  <div className="bg-blue-50 border border-blue-200 rounded p-3 text-sm">
                    <p className="text-blue-900">
                      ‚ÑπÔ∏è –û–±—Ä–∞–Ω–æ {selectedPlayers.length} –≥—Ä–∞–≤—Ü—ñ–≤. –†–µ–∫–æ–º–µ–Ω–¥–æ–≤–∞–Ω–∞ —Å—ñ—Ç–∫–∞: <strong>{suggestedBracketSize}</strong> 
                      {suggestedBracketSize > selectedPlayers.length && 
                        ` (+ ${suggestedBracketSize - selectedPlayers.length} walk-over)`}
                    </p>
                  </div>
                )}
                
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-900 mb-1">
                      –†–æ–∑–º—ñ—Ä Upper Bracket
                    </label>
                    <input
                      type="number"
                      name="upper_bracket_size"
                      required
                      min="2"
                      max="128"
                      value={suggestedBracketSize}
                      onChange={(e) => setSuggestedBracketSize(parseInt(e.target.value))}
                      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 text-gray-900"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-900 mb-1">
                      –†–æ–∑–º—ñ—Ä Olympic Bracket
                    </label>
                    <input
                      type="number"
                      name="olympic_size"
                      required
                      min="2"
                      max="64"
                      defaultValue="16"
                      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 text-gray-900"
                    />
                  </div>
                </div>
              </div>

              {/* Stage Rules */}
              <div className="bg-gray-50 rounded-lg p-4 space-y-4">
                <h3 className="font-semibold text-gray-900">–ü—Ä–∞–≤–∏–ª–∞ –µ—Ç–∞–ø—ñ–≤ (–¥–æ —Å–∫—ñ–ª—å–∫–æ—Ö –ø–∞—Ä—Ç—ñ–π)</h3>
                
                <div className="grid grid-cols-3 gap-3">
                  <div>
                    <label className="block text-xs font-medium text-gray-700 mb-1">Upper Bracket</label>
                    <input
                      type="number"
                      min="1"
                      max="100"
                      value={stageRules.upper}
                      onChange={(e) => setStageRules({...stageRules, upper: parseInt(e.target.value)})}
                      className="w-full px-2 py-1 text-sm border border-gray-300 rounded focus:ring-2 focus:ring-blue-500 text-gray-900"
                    />
                  </div>
                  <div>
                    <label className="block text-xs font-medium text-gray-700 mb-1">Lower Bracket</label>
                    <input
                      type="number"
                      min="1"
                      max="100"
                      value={stageRules.lower}
                      onChange={(e) => setStageRules({...stageRules, lower: parseInt(e.target.value)})}
                      className="w-full px-2 py-1 text-sm border border-gray-300 rounded focus:ring-2 focus:ring-blue-500 text-gray-900"
                    />
                  </div>
                  <div>
                    <label className="block text-xs font-medium text-gray-700 mb-1">1/4 –§—ñ–Ω–∞–ª—É</label>
                    <input
                      type="number"
                      min="1"
                      max="100"
                      value={stageRules.quarter}
                      onChange={(e) => setStageRules({...stageRules, quarter: parseInt(e.target.value)})}
                      className="w-full px-2 py-1 text-sm border border-gray-300 rounded focus:ring-2 focus:ring-blue-500 text-gray-900"
                    />
                  </div>
                  <div>
                    <label className="block text-xs font-medium text-gray-700 mb-1">–ü—ñ–≤—Ñ—ñ–Ω–∞–ª—É</label>
                    <input
                      type="number"
                      min="1"
                      max="100"
                      value={stageRules.semi}
                      onChange={(e) => setStageRules({...stageRules, semi: parseInt(e.target.value)})}
                      className="w-full px-2 py-1 text-sm border border-gray-300 rounded focus:ring-2 focus:ring-blue-500 text-gray-900"
                    />
                  </div>
                  <div>
                    <label className="block text-xs font-medium text-gray-700 mb-1">–§—ñ–Ω–∞–ª—É</label>
                    <input
                      type="number"
                      min="1"
                      max="100"
                      value={stageRules.final}
                      onChange={(e) => setStageRules({...stageRules, final: parseInt(e.target.value)})}
                      className="w-full px-2 py-1 text-sm border border-gray-300 rounded focus:ring-2 focus:ring-blue-500 text-gray-900"
                    />
                  </div>
                </div>
              </div>

              {/* Player Selection */}
              <div className="bg-gray-50 rounded-lg p-4">
                <div className="flex justify-between items-center mb-3">
                  <h3 className="font-semibold text-gray-900">
                    –£—á–∞—Å–Ω–∏–∫–∏ —Ç—É—Ä–Ω—ñ—Ä—É ({selectedPlayers.length} –æ–±—Ä–∞–Ω–æ)
                  </h3>
                  {selectedPlayers.length > 0 && (
                    <button
                      type="button"
                      onClick={() => setSelectedPlayers([])}
                      className="text-sm text-red-600 hover:text-red-700"
                    >
                      –û—á–∏—Å—Ç–∏—Ç–∏
                    </button>
                  )}
                </div>

                {/* Search */}
                <div className="mb-3">
                  <input
                    type="text"
                    placeholder="üîç –®—É–∫–∞—Ç–∏ –≥—Ä–∞–≤—Ü—è..."
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 text-gray-900 placeholder-gray-400"
                  />
                </div>
                
                <div className="grid grid-cols-2 md:grid-cols-3 gap-2 max-h-64 overflow-y-auto border border-gray-200 rounded p-3 bg-white">
                  {!players || players.length === 0 ? (
                    <p className="text-gray-500 text-sm col-span-full text-center py-4">
                      –ù–µ–º–∞—î –¥–æ—Å—Ç—É–ø–Ω–∏—Ö –≥—Ä–∞–≤—Ü—ñ–≤
                    </p>
                  ) : (
                    players
                      .filter(player => 
                        player.name.toLowerCase().includes(searchQuery.toLowerCase())
                      )
                      .sort((a, b) => b.rating - a.rating)
                      .map(player => (
                        <label
                          key={player.id}
                          className={`flex items-center gap-2 p-2 rounded cursor-pointer transition-colors ${
                            selectedPlayers.includes(player.id)
                              ? 'bg-blue-50 border border-blue-300'
                              : 'hover:bg-gray-50 border border-transparent'
                          }`}
                        >
                          <input
                            type="checkbox"
                            checked={selectedPlayers.includes(player.id)}
                            onChange={() => togglePlayer(player.id)}
                            className="w-4 h-4 text-blue-600 rounded"
                          />
                          <div className="flex-1 min-w-0">
                            <p className="text-sm font-medium text-gray-900 truncate">
                              {player.name}
                            </p>
                            <p className="text-xs text-gray-500">
                              {Math.round(player.rating)}
                            </p>
                          </div>
                        </label>
                      ))
                  )}
                </div>
                
                {selectedPlayers.length > 0 && (
                  <p className="text-xs text-gray-500 mt-2">
                    üí° –û–±—Ä–∞–Ω—ñ –≥—Ä–∞–≤—Ü—ñ –±—É–¥—É—Ç—å –¥–æ–¥–∞–Ω—ñ –¥–æ —Ç—É—Ä–Ω—ñ—Ä—É –ø—ñ—Å–ª—è –π–æ–≥–æ —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è
                  </p>
                )}
              </div>

              {/* Actions */}
              <div className="flex gap-3 pt-4 border-t">
                <button
                  type="button"
                  onClick={() => {
                    setShowCreateModal(false);
                    setSelectedPlayers([]);
                  }}
                  className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50"
                >
                  –°–∫–∞—Å—É–≤–∞—Ç–∏
                </button>
                <button
                  type="submit"
                  disabled={selectedPlayers.length < 2}
                  className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 font-semibold disabled:bg-gray-300 disabled:cursor-not-allowed"
                >
                  –°—Ç–≤–æ—Ä–∏—Ç–∏ —Ç—É—Ä–Ω—ñ—Ä {selectedPlayers.length < 2 && `(–ø–æ—Ç—Ä—ñ–±–Ω–æ —â–µ ${2 - selectedPlayers.length})`}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
